$primary-color: rgb(6, 40, 61);
$secondary-color: #689384;
$tertiary-color: #5a4741;
$quaternary-color: rgb(223, 246, 255);


body{
    background-color: #689384;

}

.main{
    color:$quaternary-color;
    margin-left: 20%;
    margin-right: 20%;
    margin-top: 20px;
    border: solid $tertiary-color 0.5px;
    background-color: #5a4741;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    border-radius: 20px;
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.articles{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: nowrap;
}

.article:nth-child(even){
    align-self: flex-end;
}

.article{
    height: 450px;
    width: 700px;
    margin: 30px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    -webkit-filter: grayscale(100%);
	filter: grayscale(100%);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
}

.article__title {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    text-transform: uppercase;
    margin: 0;
    font-size: 3rem;
    white-space: wrap;
}

.article:hover{
	-webkit-filter: grayscale(0);
	filter: grayscale(0);
    box-shadow: 10px 10px 10px $secondary-color;
}



.article:nth-child(1){
    background-image: url('../../images/DSC02108-1024x683.jpg');
}

.article:nth-child(2){
    background-image: url('../../images/DSC05083-1024x683.jpg');
}

.article:nth-child(3){
    background-image: url('../../images/DSC05303-2-1024x819.jpg');
}

.article:nth-child(4){
    background-image: url('../../images/IMG_3416.jpg');
}

.article:nth-child(5){
    background-image: url('../../images/IMG_3428.jpg');
}

.article:nth-child(6){
    background-image: url('../../images/IMG_7887-2-1.jpg');
}

.article__text {
    padding-top: 20px;
    padding-left: 20px;
    padding-right: 20px;
    display: none;
}

